@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital@1&display=swap);

body {
  background-image: -webkit-gradient(linear, left bottom, right top, from(#000000), color-stop(#b33131), to(#cec1c1));
  background-image: linear-gradient(to top right, #000000, #b33131, #cec1c1);
  background-attachment: fixed;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Titillium Web',sans-serif;
  text-align: center;
  margin: 35px;
}

