.profile-picture {
  width: 250px;
  height: 250px;
  border-radius: 30%;
  object-fit: cover;
  object-position: center right;
}

.main-container #social-media {
  z-index: 1;
}

.main-container #particles {
  position: fixed;
  height: 100%;
  width: 100%;
}
