/* No CSS *//*# sourceMappingURL=about.css.map */
.container{
    background-color: rgb(221, 219, 219);
    margin-bottom: 50px;
    border-radius: 10px;
    font-size: 20px;

}

.about-me{
    text-align: center;
    width: 50%;
    margin: auto;

}


.stars span{
    float: right;
    margin-right: 30%; 
}
.stars p{
    font-weight: 600;
    margin-bottom: 5px;
}
.tab{
    margin-left: 40px;
}


/* education part to line up the text */
.education{
    text-align: left;
}
.education h1, .skills h1{
    text-align: center;
}

.education p{
    font-weight: 500;

}

.date{
    font-weight: 200;
    font-style: italic;
}

.skills{
    text-align: left;
}

.skills ul li{
    list-style: none;
}

.stars{
    align-items: center;
}
.projects-container {
  background-color: #dddbdb;
  width: 90%;
  margin: auto;
  padding: 5px 0;
  border-radius: 10px;
}

.projects-container .cards {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.projects-container .cards .card-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}

.projects-container .cards .card-wrapper .description {
  text-align: center;
  margin: 10px;
}

.projects-container .cards .card-wrapper .description .stack {
  font-weight: 800;
}

.projects-container .cards .card-wrapper .description .project-title {
  text-decoration: underline;
}

.projects-container .cards .card-wrapper img {
  width: 500px;
  height: 500px;
}
